<template>
<div>
    <v-container fluid>
        <v-row>
            <v-col class="d-flex">
                <v-select :items="clusters" item-value="id" item-text="name" label="选择集群" solo v-model="clusterid"></v-select>
            </v-col>
        </v-row>
        <v-row v-if="cluster != null">
            <v-card :loading="loading" style="width: 100%;">
                <v-card-title>{{cluster.namespace}}</v-card-title>
                <v-card-text>
                    <div>{{cluster.description}}</div>
                </v-card-text>
                <v-divider class="mx-4"></v-divider>
                <v-card-title>注册中心</v-card-title>
                <v-card-text> {{cluster.register}} </v-card-text>
                <v-card-title>在线服务数量 </v-card-title>
                <v-card-text> {{cluster.services!=null?cluster.services.length:0}} </v-card-text>
                <v-row style="width: 96%;margin-left: 2%;margin-bottom: 20px">
                    <v-col v-for="({ actionIcon, actionText, ...attrs }, i) in stats" :key="i" cols="12" md="6" lg="4">
                        <material-stat-card v-bind="attrs" class="stat-card">
                            <template #actions>
                                <v-icon class="mr-3" small v-text="actionIcon" />
                                <div class="text-truncate">
                                    {{ actionText }}
                                </div>
                                <v-btn class="mx-2" fab dark color="primary" style="position: absolute;right: 10px;" @click="showService(attrs)">
                                    <v-icon dark>
                                        mdi-format-list-bulleted-square
                                    </v-icon>
                                </v-btn>
                            </template>
                        </material-stat-card>
                    </v-col>
                </v-row>
            </v-card>
        </v-row>
    </v-container>
    <v-dialog v-model="serviceDetailDialog" fullscreen hide-overlay transition="dialog-bottom-transition">
        <v-overlay :value="overlay"></v-overlay>
        <v-card>
            <v-toolbar dark color="primary">
                <v-btn icon dark @click="serviceDetailDialog = false">
                    <v-icon>mdi-close</v-icon>
                </v-btn>
                <v-toolbar-title>服务详情</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-toolbar-items>
                    <!--       <v-btn dark text @click="serviceDetailDialog = false">
            </v-btn> -->
                </v-toolbar-items>
            </v-toolbar>
            <v-list three-line subheader>
                <v-subheader>服务信息</v-subheader>
                <v-list-item>
                    <v-list-item-content>
                        <v-list-item-title>当前连接客户端数</v-list-item-title>
                        <v-list-item-subtitle>{{serviceDetailMap.client_count}}</v-list-item-subtitle>
                    </v-list-item-content>
                </v-list-item>
            </v-list>
            <v-divider></v-divider>
            <v-row style="width: 96%;margin-left: 2%;margin-top: 20px">
                <v-col cols="12" md="6" lg="4">
                    <v-treeview rounded hoverable selectable return-object activatable open-on-click transition :items="serviceDetail" :active.sync="active" v-model="selects" selected-color="#2196f3" color="#2196f3"></v-treeview>
                    <v-btn depressed color="primary" style="margin-top: 30px;margin-left: 20px;" @click="deleteItem">
                        删除
                    </v-btn>
                </v-col>
                <v-col cols="12" md="6" lg="4" v-if="showType == 'config'">
                    <v-jsoneditor v-model=" configVal" :options="options" :plus="true" height="600px" />
                    <v-btn depressed color="primary" style="margin-top: 30px; margin-bottom: 10px;" @click="saveChange">
                        微调保存
                    </v-btn>
                    <v-alert type="warning">
                        微调会导致配置数据不一致和版本化明确，建议别调，走正式发布路线
                    </v-alert>
                </v-col>
                <v-col cols="12" md="6" lg="4" v-if="showType == 'filter'">
                    <codemirror v-model=" filterVal"  :options="{mode: 'lua', extraKeys: {'Ctrl-Space': 'autocomplete'},lineNumbers:true,theme:'mdn-like'}">
                    </codemirror>
                    <v-btn depressed color="primary" style="margin-top: 30px;margin-bottom: 10px;" @click="saveChange">
                        微调保存
                    </v-btn>
                    <v-alert type="warning">
                        微调会导致配置数据不一致和版本化不明确，建议走正式发布路线
                    </v-alert>
                </v-col>
            </v-row>
        </v-card>
    </v-dialog>
</div>
</template>

<script>
import axios from "axios"


require('codemirror/mode/css/css')
require('codemirror/theme/mdn-like.css')
require('codemirror/mode/lua/lua')

export default {
    data: () => ({
        overlay: false,
        currentService: "",
        selects: [],
        active: [],
        showType: "none",
        configVal: [],
        filterVal: "",
        serviceDetailDialog: false,
        loading: false,
        clusters: [],
        cluster: {
            name: "测试环境",
            desc: "描述1111111",
            register: "etcd://127.0.0.1/mconfig",
            servers: 100,
            status: [{
                    name: "正常",
                    color: "green"
                },
                {
                    name: "高可用",
                    color: "primary"
                },
            ]
        },
        options: {
            mode: "code"
        },
        clusterid: 0,
        stats: [],
        service: [],
        serviceDetail: [],
        serviceDetailMap: {},
    }),
    mounted() {
        this.clusterid = parseInt(this.$route.params.cluster)
        this.getClusterList()
    },
    watch: {
        clusterid: {
            handler() {
                this.getClusterData()
            },
            deep: true,
        },
        active: {
            handler() {
                if (this.active.length == 1) {
                    this.filterVal = ""
                    this.configVal = []
                    console.log(this.active[0])
                    var select = this.active[0]
                    if (select.type == "config") {
                        this.showType = select.type;
                        var data = this.serviceDetailMap
                        this.configVal = JSON.parse(data['apps'][select.app][select.env]['configs'][select.name])
                    }
                    if (select.type == "filter") {
                        this.showType = select.type;
                        var data = this.serviceDetailMap
                        this.filterVal = data['apps'][select.app][select.env]['filter']
                    }
                }

            },
            deep: true,
        },
    },
    methods: {
        getClusterList() {
            //获取集群列表
            var _this = this
            axios.get('/api/v1/cluster/list', { // 还可以直接把参数拼接在url后边
                limit: 100000,
            }).then(function (res) {
                console.log(res.data.data)
                var clusters = res.data.data
                for (var i = 0; i < clusters.length; i++) {
                    clusters[i]["name"] = clusters[i]["namespace"] + " -- " + clusters[i]["register"]
                }
                _this.clusters = clusters
            }).catch(function (error) {
                console.log(error);
            });
        },
        getClusterData() {
            //获取集群信息
            var _this = this
            axios.get('/api/v1/cluster/self/' + _this.clusterid, { // 还可以直接把参数拼接在url后边
            }).then(function (res) {
                _this.cluster = res.data.data
                _this.stats = []
                if (_this.cluster.services != null) {
                    var services = _this.cluster.services;
                    var stats = []
                    for (var i = 0; i < services.length; i++) {
                        var serviceMode = services[i].metadata.mode
                        if (serviceMode == "local") {
                            stats.push({
                                // actionIcon: 'mdi-local',
                                actionText: "mconfig-server v1.0",
                                color: '#FD9A13',
                                icon: 'mdi-account-lock',
                                title: serviceMode,
                                value: services[i].address,
                            })
                        }
                        if (serviceMode == "share") {
                            stats.push({
                                // actionIcon: 'mdi-local',
                                actionText: "mconfig-server v1.0",
                                color: '#f15b6c',
                                icon: 'mdi-share',
                                title: serviceMode,
                                value: services[i].address,
                            })
                        }
                    }
                    _this.stats = stats
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        showService(service) {
            console.log(service)
            //获取集群列表
            this.getServiceData(service.value)
        },

        getServiceData(address) {
            var _this = this

            axios.post('/api/v1/service/detail', { // 还可以直接把参数拼接在url后边
                cluster: _this.clusterid,
                service: address,
            }).then(function (res) {
                console.log(res.data.data)
                var data = res.data.data
                var serviceDetail = []
                var apps = data.apps
                var id = 0;

                for (var appKey in apps) {
                    var ser = {
                        id: id++,
                        name: "app: " + appKey,
                        children: [],
                    }
                    var envs = []
                    for (var envKey in apps[appKey]) {
                        var env = {
                            id: id++,
                            name: "env: " + envKey,
                            children: [],
                        }
                        var configs = []
                        for (var config in apps[appKey][envKey].configs) {
                            configs.push({
                                id: id++,
                                name: config,
                                app: appKey,
                                env: envKey,
                                type: "config",
                            })
                        }
                        // console.log(configs)
                        env.children.push({
                            id: id++,
                            name: 'configs',
                            children: configs,
                        })
                        env.children.push({
                            id: id++,
                            name: 'filter',
                            app: appKey,
                            env: envKey,
                            type: "filter",
                        })
                        envs.push(env)
                    }
                    ser.children = envs
                    serviceDetail.push(ser)

                }
                _this.serviceDetail = serviceDetail
                _this.serviceDetailDialog = true
                _this.serviceDetailMap = data

                _this.currentService = address

            }).catch(function (error) {
                console.log(error);
            });
        },

        saveChange() {

            var _this = this
            _this.overlay = true
            var app = ""
            var env = ""
            var filter = ""
            var config = ""
            var val = ""

            if (this.active.length == 1) {
                var select = this.active[0]
                app = select.app
                env = select.env

                if (select.type == "config") {
                    var data = this.serviceDetailMap
                    config = select.name
                    console.log(this.configVal)
                    val = JSON.stringify(this.configVal)
                    filter = data['apps'][select.app][select.env]['filter']
                }
                if (select.type == "filter") {
                    var data = this.serviceDetailMap
                    filter = this.filterVal
                }
            }

            axios.post('/api/v1/service', {
                service: _this.currentService,
                app: app,
                env: env,
                filter: filter,
                config: config,
                configval: val,
            }).then(function (res) {
                console.log(res.data.data)
                _this.overlay = false
                alert("微调成功")
                this.getServiceData(_this.currentService)
            }).catch(function (error) {
                _this.overlay = false
                console.log(error);
            });
        },
        deleteItem() {
            console.log(this.selects)
            var selects = this.selects
            for (var i = 0; i < selects.length; i++) {
                var select = selects[i]
                console.log(select)
                var _this = this
                axios.post('/api/v1/service/delete', { // 还可以直接把参数拼接在url后边
                    service: _this.currentService,
                    type: select.type,
                    app: select.app,
                    env: select.env,
                    config: select.name,
                }).then(function (res) {
                    console.log(res.data.data)
                    alert("删除成功")
                    _this.getServiceData(_this.currentService)

                }).catch(function (error) {
                    console.log(error);
                });
            }
        }
    }
}
</script>

<style scoped="">
</style>
